// Questionnaire.tsx
import React, { useState } from 'react';
import styled from 'styled-components';
import jsPDF from 'jspdf';

const Container = styled.div`
  padding: 20px;
  max-width: 600px;
  margin: auto;
`;

const QuestionContainer = styled.div`
  margin-bottom: 20px;
`;

const TextArea = styled.textarea`
  width: 100%;
  height: 100px;
  margin-top: 5px;
`;

const Button = styled.button`
  padding: 10px 20px;
  background-color: #4caf50;
  color: white;
  border: none;
  cursor: pointer;

  &:hover {
    background-color: #45a049;
  }
`;

interface SubQuestion {
  text: string;
}

interface Question {
  text: string;
  subQuestions?: SubQuestion[];
}
const questions: Question[] = [
    {
      text: "Se apresenta pra gente, me conta um pouco sobre voc.",
      subQuestions: [
        { text: "Como  sua rotina no dia a dia? Fale um pouco sobre quais atividades voc faz alm das aulas na UFRJ e como elas se encaixam na sua rotina." },
        { text: "Identifique na fala do candidato e pergunte se for preciso se ele aparenta ter boa gesto das atividades e se tem disponibilidade de 20h semanais para dedicao na EJCM." }
      ]
    },
    {
      text: "O que voc achou de participar do dia de dinmicas?",
      subQuestions: [
        { text: "Qual foi sua maior dificuldade e qual foi sua maior facilidade?" },
        { text: "Como foi escrever a resoluo de case?" },
        { text: "Voc usou alguma ferramenta para te auxiliar?" }
      ]
    },
    { text: "Qual sua motivao para fazer parte da EJCM?" },
    {
      text: "Voc j conhecia a EJCM ou o Movimento Empresa Jnior, o MEJ?",
      subQuestions: [
        { text: "Qual a ideia que voc tem da EJCM e do Movimento Empresa Jnior?" },
      ]
    },
    {
      text: "Quais so seus objetivos de vida em longo e curto prazo?",
      subQuestions: [
        { text: "Como a EJCM te ajudaria com esses objetivos?" }
      ]
    },
    {
      text: "Dado os nossos valores:",
      subQuestions: [
        { text: "Em qual deles voc mais se reconhece?" },
        { text: "Em qual voc acha que poderia se desenvolver mais?" },
        { text: "Por que?" }
      ]
    },
    { text: "Descreva um momento em que voc teve que tomar uma deciso rpida para resolver uma situao." },
    {
      text: "Como as pessoas te enxergam?",
      subQuestions: [
        { text: "Voc concorda com essa percepo?" },
        { text: "Por que?" },
        { text: "Como isso te afeta?" }
      ]
    },
    {
      text: "Qual foi o melhor chefe/professor/figura de autoridade que voc j teve?",
      subQuestions: [
        { text: "Como ele(a) era?" }
      ]
    },
    {
      text: "Conte-me uma situao em que voc contribuiu com suas prprias ideias ou atitudes, sem receber instrues."
    },
    {
      text: "O que diversidade e incluso significam para voc e qual a importncia disso?"
    },
    { text: "Qual sua opinio sobre trabalhar em um ambiente diversificado?" },
    {
      text: "Diante de um comentrio racista ou homofbico dentro da empresa, qual  a sua reao?"
    },
    {
      text: "Por que voc acha que deveria ser aprovado?"
    }
  ];
  

const PAGE_HEIGHT = 290; // Altura da pgina em milmetros, ajuste conforme necessrio

const Questionnaire: React.FC = () => {
  const [responses, setResponses] = useState<string[]>(Array(questions.length).fill(''));
  const [subResponses, setSubResponses] = useState<string[][]>(questions.map(() => [])); // Respostas para subperguntas

  const handleChange = (index: number, value: string) => {
    const updatedResponses = [...responses];
    updatedResponses[index] = value;
    setResponses(updatedResponses);
  };

  const handleSubChange = (questionIndex: number, subIndex: number, value: string) => {
    const updatedSubResponses = [...subResponses];
    if (!updatedSubResponses[questionIndex]) {
      updatedSubResponses[questionIndex] = [];
    }
    updatedSubResponses[questionIndex][subIndex] = value;
    setSubResponses(updatedSubResponses);
  };

  const handleGeneratePDF = () => {
    const doc = new jsPDF();
    let verticalPosition = 10; // Posio inicial na pgina
    const margin = 10; // Margem para o texto
    const pageHeight = doc.internal.pageSize.getHeight(); // Altura total da pgina
    const pageWidth = doc.internal.pageSize.getWidth() - 2 * margin; // Largura da pgina com margens
  
    // Define o tamanho da fonte
    const fontSize = 10; // Tamanho da fonte em milmetros
    doc.setFontSize(fontSize); // Aplica o tamanho da fonte
  
    questions.forEach((question, index) => {
      // Adiciona nova pgina se necessrio
      if (verticalPosition + 40 > pageHeight) { // 40 para permitir perguntas e espao
        doc.addPage();
        verticalPosition = 10; // Reinicia a posio vertical
      }
  
      // Quebra a pergunta em vrias linhas
      const questionLines = doc.splitTextToSize(`Pergunta ${index + 1}: ${question.text}`, pageWidth);
  
      // Define a fonte como negrito para a pergunta
      doc.setFont("helvetica", "bold"); // Altere a fonte para negrito
  
      questionLines.forEach((line: string, lineIndex: number) => {
        doc.text(line, margin, verticalPosition + (lineIndex * (fontSize * 0.75))); // Adiciona cada linha da pergunta
      });
  
      // Define a fonte como normal para a resposta
      doc.setFont("helvetica", "normal"); // Altere a fonte de volta para normal
      verticalPosition += questionLines.length * (fontSize * 0.75) + 2; // Ajuste aqui para diminuir a distncia
  
      // Quebra a resposta em vrias linhas
      const responseLines = doc.splitTextToSize(`Resposta: ${responses[index]}`, pageWidth);
      responseLines.forEach((line: string, lineIndex: number) => {
        doc.text(line, margin, verticalPosition + (lineIndex * (fontSize * 0.75))); // Adiciona cada linha da resposta
      });
      verticalPosition += responseLines.length * (fontSize * 0.75) + 2; // Ajuste aqui para diminuir a distncia
  
      if (question.subQuestions) {
        question.subQuestions.forEach((subQuestion, subIndex) => {
          // Verifica se precisa adicionar uma nova pgina
          if (verticalPosition + 30 > pageHeight) { // 30 para garantir espao para a subpergunta
            doc.addPage();
            verticalPosition = 10; // Reinicia a posio vertical
          }
  
          verticalPosition += 5; // Espao adicional antes da subpergunta
          // Quebra a subpergunta em vrias linhas
          doc.setFont("helvetica", "bold");
          const subQuestionLines = doc.splitTextToSize(`  Subpergunta ${subIndex + 1}: ${subQuestion.text}`, pageWidth);
          subQuestionLines.forEach((line: string, lineIndex: number) => {
            doc.text(line, margin + 10, verticalPosition + (lineIndex * (fontSize * 0.75))); // Adiciona cada linha da subpergunta
          });
          verticalPosition += subQuestionLines.length * (fontSize * 0.75) + 5; // Adiciona altura da subpergunta e espao extra
          doc.setFont("helvetica", "normal");
  
          // Quebra a subresposta em vrias linhas
          const subResponseLines = doc.splitTextToSize(`  Resposta: ${subResponses[index]?.[subIndex] || ''}`, pageWidth);
          subResponseLines.forEach((line: string, lineIndex: number) => {
            doc.text(line, margin + 10, verticalPosition + (lineIndex * (fontSize * 0.75))); // Adiciona cada linha da subresposta
          });
          verticalPosition += subResponseLines.length * (fontSize * 0.75) + 5; // Adiciona altura da subresposta e espao extra
        });
      }
  
      verticalPosition += 10; // Espao adicional aps todas as perguntas e subperguntas
    });
  
    doc.save('respostas.pdf');
  };
  
  
  return (
    <Container>
      <h1>Perguntas</h1>
      {questions.map((question, index) => (
        <QuestionContainer key={index}>
          <label>{question.text}</label>
          <TextArea
            value={responses[index]}
            onChange={(e) => handleChange(index, e.target.value)}
          />
          {/* Renderizar subperguntas sempre, independentemente da resposta */}
          {question.subQuestions && (
            question.subQuestions.map((subQuestion, subIndex) => (
              <QuestionContainer key={subIndex} style={{ marginLeft: '20px' }}>
                <label>{subQuestion.text}</label>
                <TextArea
                  value={subResponses[index][subIndex] || ''}
                  onChange={(e) => handleSubChange(index, subIndex, e.target.value)}
                />
              </QuestionContainer>
            ))
          )}
        </QuestionContainer>
      ))}
      <Button onClick={handleGeneratePDF}>Gerar PDF</Button>
    </Container>
  );
};

export default Questionnaire;
